<template>
  <div class="login-wrap">
    <div class="login_setting">
      <van-icon name="setting-o" @click="showTypeChange(0)" />
    </div>
    <div class="logo"></div>
    <div class="loginForm">
      <div class="formItem">
        <van-field
          v-model="accountNo"
          placeholder="账号"
          :error-message="accountNoErr"
          required
          @keyup.enter.native="loginSubmit"
        >
          <van-icon slot="left-icon" class-prefix="iconfont iconwode-weixuanzhong" />
        </van-field>
      </div>

      <div class="formItem">
        <van-field
          v-model="password"
          type="password"
          placeholder="密码"
          :error-message="passwordErr"
          required
          @keyup.enter.native="loginSubmit"
        >
          <van-icon slot="left-icon" class-prefix="iconfont iconmima" />
        </van-field>
      </div>
      <!-- <div class="formItem">
        <van-field
          v-model="serverIP"
          placeholder="服务IP"
          :error-message="serverIPErr"
          required
          @keyup.enter.native="loginSubmit"
        >
          <van-icon slot="left-icon" class-prefix="iconfont iconIP1" />
        </van-field>
      </div>-->
    </div>
    <div class="footer">
      <van-button
        type="primary"
        :loading="loading"
        @click="loginSubmit"
        :disabled="disabled"
        loading-text="登录中..."
      >登录</van-button>
      <div class="footer_operate">
        <span @click="showTypeChange(2)">用户注册</span>
      </div>
    </div>
  </div>
</template>

<script>
import { Field, Icon, Button } from "vant";
import md5 from "js-md5";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { encrypt, decryption } from "@/utils/index";
export default {
  name: "r-login",
  components: { [Field.name]: Field, [Button.name]: Button, [Icon.name]: Icon },
  created() {},
  data() {
    return {
      accountNo: this.$store.getters.userName || "",
      accountNoErr: "",
      password: decryption(this.$store.getters.userPassword || ""),
      passwordErr: "",
      // serverIP: this.$store.getters.serverIP || "",
      // serverIPErr: "",
      disabled: false,
      loading: false
    };
  },
  methods: {
    showTypeChange(val) {
      this.$emit("change", val);
    },
    loginSubmit() {
      this.accountNoErr = "";
      this.passwordErr = "";
      // this.serverIPErr = "";
      if (this.accountNo === "") {
        this.accountNoErr = "请输入登录账号";
        return;
      }
      if (this.password === "") {
        this.passwordErr = "请输入登录密码";
        return;
      }
      if (!this.$store.getters.serverIP) {
        this.$toast("请先设置服务地址");
        this.showTypeChange(0);
        return;
      }
      // if (this.serverIP === "") {
      //   this.serverIPErr = "请输入服务IP";
      //   return;
      // }
      // this.$store.commit("SET_SERVERIP", this.serverIP);
      if (this.loading) return;
      this.loading = true;
      this.disabled = true;
      this.$store
        .dispatch("LoginByUser", {
          name: this.accountNo,
          pwd: md5(this.password.toUpperCase()),
          from: "22"
        })
        .then(res => {
          if (res.Result) {
            this.$store.commit("SET_USERPASSWORD", encrypt(this.password));
            this.$router.push("/index");
            sendToAPP(FuncType.hadLogin, "");
          } else if (res.Info) {
            this.$toast("账号或密码错误");
          } else {
            this.$toast("账号或密码错误");
          }
          this.disabled = false;
          this.loading = false;
        })
        .catch(err => {
          this.disabled = false;
          this.loading = false;
        });
    }
  }
};
</script>

<style lang="less">
</style>



