<template>
  <div class="login-wrap">
    <div class="login_setting">
      <van-icon name="setting-o" @click="showTypeChange(0)" />
    </div>
    <div class="logo"></div>
    <div class="loginForm">
      <div class="formItem">
        <van-field v-model="accountNo" placeholder="学号" :error-message="accountNoErr" required>
          <van-icon slot="left-icon" class-prefix="iconfont icongerenziliao" />
        </van-field>
      </div>

      <div class="formItem">
        <van-field v-model="username" placeholder="姓名" :error-message="usernameErr" required>
          <van-icon slot="left-icon" class-prefix="iconfont iconwode-weixuanzhong" />
        </van-field>
      </div>

      <div class="formItem">
        <van-field
          v-model="password"
          type="password"
          placeholder="密码"
          :error-message="passwordErr"
          required
        >
          <van-icon slot="left-icon" class-prefix="iconfont iconmima" />
        </van-field>
      </div>
      <div class="formItem">
        <van-field
          v-model="rePassword"
          type="password"
          placeholder="确认密码"
          :error-message="rePasswordErr"
          required
          @keyup.enter.native="registerSubmit"
        >
          <van-icon slot="left-icon" class-prefix="iconfont iconmima" />
        </van-field>
      </div>
    </div>
    <div class="footer">
      <van-button
        type="primary"
        :loading="loading"
        @click="registerSubmit"
        :disabled="disabled"
        loading-text="注册中..."
      >{{btnName}}</van-button>
      <div class="footer_operate">
        <span @click="showTypeChange(1)">返回登录</span>
      </div>
    </div>
  </div>
</template>

<script>
import { Field, Icon, Button } from "vant";
export default {
  name: "r-register",
  components: { [Field.name]: Field, [Button.name]: Button, [Icon.name]: Icon },
  created() {},
  data() {
    return {
      accountNo: "",
      accountNoErr: "",
      username: "",
      usernameErr: "",
      password: "",
      passwordErr: "",
      rePassword: "",
      rePasswordErr: "",
      studentID: "",
      btnName: "注册",
      disabled: false,
      loading: false
    };
  },
  methods: {
    showTypeChange(val) {
      this.$emit("change", val);
    },
    registerSubmit() {
      this.accountNoErr = "";
      this.usernameErr = "";
      this.passwordErr = "";
      this.rePasswordErr = "";
      if (this.accountNo === "") {
        this.accountNoErr = "请输入学号";
        return;
      }
      if (this.username === "") {
        this.usernameErr = "请输入姓名";
        return;
      }
      if (this.password === "") {
        this.passwordErr = "请输入登录密码";
        return;
      }
      if (this.rePassword === "") {
        this.rePasswordErr = "请输入确认密码";
        return;
      }
      if (this.rePassword !== this.password) {
        this.rePasswordErr = "两次输入的密码不一致";
        return;
      }
      if (!this.$store.getters.serverIP) {
        this.$toast("请先设置服务地址");
        this.showTypeChange(0);
        return;
      }
      this.loading = true;
      this.disabled = true;
      this.verifyUser();
    },
    verifyUser() {
      this.$mainHttp.classStudent
        .getVerifyData({ xh: this.accountNo })
        .then(res => {
          if (res.Result) {
            this.studentID = res.Data["StudentID"];
            this.postRegister();
          } else {
            this.accountNoErr = "学号不正确";
            this.$toast("请确认学号");
            this.disabled = false;
            this.loading = false;
          }
        })
        .catch(err => {
          this.disabled = false;
          this.loading = false;
        });
    },
    postRegister() {
      let reqData = {
        name: this.accountNo,
        id: this.studentID,
        type: 2,
        pwd: this.password
      };
      this.$mainHttp.account
        .postRegister(reqData)
        .then(res => {
          if (res.Result) {
            this.$toast("注册成功");
            this.btnName = "重新注册";
            this.$store.commit("SET_STUDENTID", this.studentID);
            this.$store.commit("SET_USERPASSWORD", "");
            // this.$emit("change", 1);
            this.disabled = false;
            this.loading = false;
          } else if (res.Info) {
            this.$toast(res.Info);
            this.disabled = false;
            this.loading = false;
          } else {
            this.$toast("注册失败");
            this.disabled = false;
            this.loading = false;
          }
        })
        .catch(err => {
          this.disabled = false;
          this.loading = false;
        });
    }
  }
};
</script>

<style lang="less">
</style>



